<template>
  <v-container id="notes" fluid tag="section">
    <v-row>
      <v-col cols="12" md="4">
        <v-card height="550px" class="mx-auto">
          <v-toolbar color="primary">
            <!-- <v-toolbar-title>My files</v-toolbar-title> -->
            <v-text-field label="搜索" color="black" hide-details style="margin-top: 8px;">
              <template v-slot:append-outer>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn class="mt-n2" elevation="1" fab small v-bind="attrs" v-on="on">
                      <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                  </template>
                  <span>搜索</span>
                </v-tooltip>
              </template>
            </v-text-field>
            <template style="margin-top: 8px;">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn elevation="1" fab small @click="add" v-bind="attrs" v-on="on">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </template>
                <span>添加笔记</span>
              </v-tooltip>
            </template>
          </v-toolbar>

          <!-- <template v-if="notes.length == 0">
            请添加笔记
          </template>
          <template v-else>

          </template> -->

          <v-list two-line>
            <v-list-item-group v-model="pk" color="primary">
              <template v-for="(note, index) in notes">
                <v-list-item :key="note.title" height="70px" @click="choose(index)">
                  <v-list-item-content>
                    <v-list-item-title v-html="note.title"></v-list-item-title>
                    <v-list-item-subtitle v-html="note.edit_date"></v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action>
                    <v-btn icon v-if="note.star">
                      <v-icon color="warning">mdi-star</v-icon>
                    </v-btn>
                  </v-list-item-action>
                  <!-- <v-divider inset v-if="index != notes.length" :key="note.title"></v-divider> -->
                </v-list-item>
                <v-divider v-if="index != notes.length" :key="index"></v-divider>
              </template>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>

      <v-col cols="12" md="8">
        <v-card height="550px">
          <div style="margin: 10px 10px;">
            <v-card-title class="`text-$subtitle-2`">
              <v-text-field v-model="title" :value="title" rows="1" row-height="20" style="margin-top: 10px;"
                :rules="rules_title" required></v-text-field>
              <v-spacer></v-spacer>
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn v-if="star" color="warning" icon @click="sta" v-bind="attrs" v-on="on" style="margin-top: 10px;">
                    <v-icon>mdi-star</v-icon>
                  </v-btn>
                  <v-btn v-else icon @click="sta" v-bind="attrs" v-on="on" style="margin-top: 10px;">
                    <v-icon>mdi-star</v-icon>
                  </v-btn>
                </template>
                <span>收藏</span>
              </v-tooltip>
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn icon @click="del" v-bind="attrs" v-on="on" style="margin-top: 10px;">
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </template>
                <span>删除</span>
              </v-tooltip>
            </v-card-title>
            <v-card-subtitle style="font-size: 12px; margin-top: 1px;">
              {{ date.length > 0 ? date : dateFormat("YYYY-mm-dd") }}
            </v-card-subtitle>

            <v-col cols="12" sm="12">
              <v-textarea counter filled clearable rows="17" row-height="17" single-line auto-grow
                style="margin-left: 5px; margin-right: 5px;" v-model="content" :value=content :rules="rules_content">
              </v-textarea>
            </v-col>
            <v-card-actions>
              <div style="align-items: center;">
                <v-btn v-if="flag9==0" height="30px" @click="submit_save" color="orange accent-3"
                  style="font-size: 14px; font-weight:bold;">
                  保存
                </v-btn>
                <v-btn v-else height="30px" @click="submit_add" color="orange accent-3"
                  style="font-size: 14px; font-weight:bold;">
                  添加
                </v-btn>
              </div>
              <v-spacer></v-spacer>

            </v-card-actions>
          </div>
        </v-card>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
  export default {
    name: 'notes',
    inject: ['reload'],
    data() {
      return {
        note: {},
        rules_title: [val => (val || '').length > 0 || '标题不能为空'],
        rules_content: [val => (val || '').length > 0 || '内容不能为空'],
        title: '',
        content: '',
        date: '',
        star: false,
        notes: [],
        pk: -1,
        flag9: 1,
        len: '',
      }
    },
    created() {
      this.get_notes()
    },
    computed: {
      // items() {
      //   return Array.from({
      //     length: this.length
      //   }, (k, v) => v + 1)
      // },
      // length() {
      //   return 10
      // },
      // reverse() {
      //   return this.notes.reverse()
      // }
    },
    methods: {
      get_notes: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_notes/',
          params: {
            stu_id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log("笔记", response.data)
            that.notes = response.data.notes.reverse()
            that.len = response.data.notes.length
          } else {
            alert("获取笔记失败！")
          }
        })
      },
      choose: function (index) {
        this.flag9 = 0;
        this.pk = index;
        this.title = this.notes[index].title;
        this.content = this.notes[index].content;
        this.date = this.notes[index].edit_date;
        this.star = this.notes[index].star;
      },
      submit_save: function () {
        var that = this;
        if (that.pk >= 0) {
          that.$axios({
            method: 'get',
            url: '/api/save_note/',
            params: {
              stu_id: that.$global.userMsg.id,
              pk: that.len - that.pk - 1,
              title: that.title,
              content: that.content,
              star: that.star,
            },
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.notes[that.pk].title = that.title
              that.notes[that.pk].content = that.content
              that.reload()
              alert("修改成功！")
            } else {
              alert("修改失败！")
            }
          })
        } else {
          alert("请选中一条笔记！")
        }   
      },
      submit_add: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/save_note/',
          params: {
            stu_id: that.$global.userMsg.id,
            title: that.title,
            content: that.content,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.notes.unshift(response.data.note)
            console.log(that.notes)
            that.reload()
            alert("添加成功！")
          } else {
            alert("添加失败！")
          }
        })
      },
      add: function () {
        this.pk = -1;
        this.flag9 = 1;
        this.title = '';
        this.content = '';
      },
      sta: function () {
        var that = this;
        if (that.notes[that.pk].star) {
          that.star = false
        } else {
          that.star = true
        }
        this.$axios({
          method: 'get',
          url: '/api/save_note/',
          params: {
            stu_id: that.$global.userMsg.id,
            pk: that.len - that.pk - 1,
            title: that.title,
            content: that.content,
            star: that.star,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.notes[that.pk].star = that.star
            if (that.star) {
              alert("收藏成功！")
            } else {
              alert("取消收藏成功！")
            }
          } else {
            alert("收藏 / 取消收藏失败！")
          }
        })
      },
      del: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/erase_note/',
          params: {
            id: that.$global.userMsg.id,
            pk: that.len - that.pk - 1,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.get_notes()
            that.reload()
            alert("删除成功！")
          } else {
            alert("删除失败！")
          }
        })
      },
      dateFormat(fmt) {
        let date = new Date()
        let ret;
        const opt = {
          "Y+": date.getFullYear().toString(), // 年
          "m+": (date.getMonth() + 1).toString(), // 月
          "d+": date.getDate().toString(), // 日
          // "H+": date.getHours().toString(), // 时
          // "M+": date.getMinutes().toString(), // 分
          // "S+": date.getSeconds().toString() // 秒
        };
        for (let k in opt) {
          ret = new RegExp("(" + k + ")").exec(fmt);
          if (ret) {
              fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
          }
        }
        return fmt;
      }
    }
  }
</script>